'use client'
import React,{useState} from 'react'

function SignIn() {
    const [but,setbut]=useState(false)
  return (
    <div>
        {but? <span>one </span>:<span> tow</span>}
      <button onClick={(e)=>{
        e.preventDefault()
        setbut(!but)
      }}>touch me</button>
    </div>
  )
}

export default SignIn
